:host {
  height: 100%;
}

::slotted(*) {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
}

:host([show_content]) ::slotted(*) {
  flex-flow: row nowrap;
  align-items: stretch;
}

::slotted(ul) {
  list-style-type: none;
}

:host([show_content]) ::slotted(ul) {
  display: flex;
  flex-flow: row nowrap;
}
